import React from 'react';

import { Container, Row, Col, Image } from 'react-bootstrap';
import logo from './img/logo_dark.png';

import team_kevin from './img/team/kevin.png';
import team_alessio from './img/team/alessio.png';
import team_stefan from './img/team/stefan.png';

function Home() {
    return (
        <div>
            <div className="pt-5 pb-2 d-flex flex-column justify-content-center align-items-center" style={{ width: '100%', height: '500px', backgroundColor: 'white' }}>
                <Image src={logo} style={{ width: '75%', maxWidth: '40rem' }} />
            </div>

            <div id="centerDiv" className="mx-auto">
                <div className="text-justify" style={{ color: 'white'}}>
                    <div className="pt-5 pb-2">
                        <h1 id="homeTitle"><strong>Time to say goodbye ðŸ‘‹</strong></h1>

                        <p className="pt-2">We've had an incredible time building KlayHosting, but our journey has now come to an end. We hope you enjoyed hosting your Discord Bot's on us as much as we did creating it.</p>
                    </div>
                    
                    <div className="pt-5 pb-2">
                        <h2><strong>Stats</strong></h2>
                        <Container>
                            <Row>
                                <Col className="statCol text-center">
                                    <h1 className="statNumber"><strong>17</strong></h1>
                                    <span className="statDesc">Total servers</span>
                                </Col>
                                <Col className="statCol text-center">
                                    <h1 className="statNumber"><strong>278,67k</strong></h1>
                                    <span className="statDesc">Total requests /mo</span>
                                </Col>
                                <Col className="statCol text-center">
                                    <h1 className="statNumber"><strong>1,11k</strong></h1>
                                    <span className="statDesc">Unique visitors /mo</span>
                                </Col>
                            </Row>
                        </Container>
                    </div>

                    <div className="pt-5 pb-2">
                        <h2><strong>Why?</strong></h2>
                        <p className="pt-2">With time we realized that despite the donations, being that from the beginning ours was a completely free service (non-profit) we could not cover the various expenses of hosting management.</p>

                        <p>But donâ€™t beat yourself up, we have big plans for the future!</p>

                        <footer class="blockquote-footer">You have until 2nd of October 2021 to backup your bot.</footer>
                    </div>

                    <div className="pt-5 pb-2">
                        <h2><strong>The team</strong></h2>
                        <Container className="pt-2">
                            <Row>
                                <Col className="text-center">
                                    <Image src={team_kevin} className="teamImages" roundedCircle />
                                    <p className="pt-2">Kevin</p>
                                    <p className="teamDiscord text-muted" style={{ marginTop: '-15px' }}>@Klay4#0909</p>
                                </Col>
                                <Col className="text-center">
                                    <Image src={team_alessio} className="teamImages" roundedCircle />
                                    <p className="pt-2">Alessio</p>
                                    <p className="teamDiscord text-muted" style={{ marginTop: '-15px' }}>@ale3497#0344</p>
                                </Col>
                                <Col className="text-center">
                                    <Image src={team_stefan} className="teamImages" roundedCircle />
                                    <p className="pt-2">Stefan</p>
                                    <p className="teamDiscord text-muted" style={{ marginTop: '-15px' }}>@Killmee1#32764</p>
                                </Col>
                            </Row>
                        </Container>
                    </div>
                </div>
            </div>

        </div>
    );
}

export default Home;